  import urls from '../../../utils/urls.js'
  export default {
    data () {
      return {
        current: 0,
				isLogin: this.$store.state.isLogin
      }
    },
    computed: {
      comment () {
        return this.$store.state.comment
      },
      userInfo () {
        return this.$store.state.userInfo
      }
    },
    methods: {
      thumbsUp: function (item) {
        var id = item.id
        // var uid = uni.getStorageSync('user').id
				// let uid = JSON.parse(uni.getStorageSync('userInfo')).id
				let id1 = '';
				if(this.isLogin){
					id1 = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id1= 0
				}
        var fuid = item.user.id
        this.current = id
        uni.request({
          url: urls.HTTP + '/comment/upcomment',
          method: 'POST',
          data: {
            cid: id,
            uid: id1,
            fuid: fuid
          },
          success: (res) => {
            // console.log(res)
            if (res.data.code === 0) {
              item.up++
            } else {
              uni.showToast({
                title: '请不要重复点赞',
                icon: 'none'
              })
            }
          }
        })
      },
      hasUp: function (i) {
        if (i === this.current) {
          return true
        } else {
          return false
        }
      },
      logoUrl: function (url) {
        return this.httpUrl + url
      }
    }
  }
